import { Link, Stack, Typography } from '@mui/material';
import Utils from '@riptech/utils';
import { useSearchParams } from 'next/navigation';
import { useRouter } from 'next/router';
import { getSession } from 'next-auth/react';
import type { FC } from 'react';
import { useEffect, useState } from 'react';
import GoogleButton from 'react-google-button';
import { useDispatch } from 'react-redux';
import Routes from '../../utils/routes';
const Login: FC = () => {
  const router = useRouter();
  const dispatch = useDispatch();
  const searchParams = useSearchParams();
  const [windowPopup, setWindowPopup] = useState<Window | null>();
  useEffect(() => {
    if (!process.env.NEXT_PUBLIC_IS_CHROME_EXTENSION_UI && searchParams.get('error') === 'Callback') {
      // this code will work only in programmatically opened windows
      window.close();
    }
  }, [searchParams]);
  useEffect(() => {
    const check = async () => {
      while (windowPopup) {
        try {
          if (windowPopup.closed) {
            const session = await getSession();
            if (session) {
              if (process.env.NEXT_PUBLIC_IS_CHROME_EXTENSION_UI) {
                // Set path directly to reload extension from index.html page
                window.location.pathname = '/index.html';
              } else {
                window.location.reload();
              }
            }
            setWindowPopup(null);
            return;
          }
        } catch (e) {
          // Cross-origin error
          // We can't read `window.closed` if origin is different (while on the Google auth page).
          // eslint-disable-next-line no-console
          console.error(e);
        }
        await Utils.sleep(1000);
      }
    };
    check();
  }, [dispatch, router, windowPopup]);
  return <Stack spacing={2} textAlign="center" data-sentry-element="Stack" data-sentry-component="Login" data-sentry-source-file="Login.tsx">
      <Stack mb={2} spacing={1.5} data-sentry-element="Stack" data-sentry-source-file="Login.tsx">
        <Typography variant="h5" data-sentry-element="Typography" data-sentry-source-file="Login.tsx">Get started or sign in</Typography>

        <Stack direction="row" spacing={0.5} data-sentry-element="Stack" data-sentry-source-file="Login.tsx">
          <Typography sx={{
          color: 'text.secondary'
        }} variant="body2" data-sentry-element="Typography" data-sentry-source-file="Login.tsx">
            Get started or access your account by signing in with Google.
          </Typography>
        </Stack>
      </Stack>

      <GoogleButton label="Sign in with Google" onClick={() => {
      setWindowPopup(window.open(`${process.env.NEXT_PUBLIC_CLIENT_URL}${Routes.AUTH_GOOGLE}`, '_blank', 'popup=1'));
    }} style={{
      color: '#fff',
      margin: 'auto',
      maxWidth: 230
    }} data-sentry-element="GoogleButton" data-sentry-source-file="Login.tsx" />

      <Typography component="div" sx={{
      color: 'text.secondary',
      textAlign: 'center',
      typography: 'caption'
    }} data-sentry-element="Typography" data-sentry-source-file="Login.tsx">
        By signing up, you agree to our{' '}
        <Link color="text.primary" href={Routes.TERMS_OF_SERVICE} target="_blank" underline="always" data-sentry-element="Link" data-sentry-source-file="Login.tsx">
          Terms of Use
        </Link>
        {' and '}
        <Link color="text.primary" href={Routes.PRIVACY_POLICY} target="_blank" underline="always" data-sentry-element="Link" data-sentry-source-file="Login.tsx">
          Privacy Policy
        </Link>
        .
      </Typography>
    </Stack>;
};
export default Login;