import type { BoxProps } from '@mui/material';
import { Box } from '@mui/material';
import type { Breakpoint } from '@mui/material/styles';
import { useTheme } from '@mui/material/styles';
import { layoutClasses } from '../classes';

// ----------------------------------------------------------------------

type MainProps = BoxProps & {
  readonly layoutQuery: Breakpoint;
};

// eslint-disable-next-line import/prefer-default-export
export function Main({
  sx,
  children,
  layoutQuery,
  ...other
}: MainProps) {
  const theme = useTheme();
  const renderContent = <Box sx={{
    py: 5,
    px: 3,
    width: 1,
    borderRadius: 2,
    display: 'flex',
    flexDirection: 'column',
    bgcolor: 'background.default',
    boxShadow: theme.customShadows.card,
    maxWidth: 'var(--layout-auth-content-width)',
    textAlign: 'center'
  }}>
      {children}
    </Box>;
  return <Box className={layoutClasses.main} component="main" sx={{
    px: 2,
    py: 5,
    zIndex: 9,
    display: 'flex',
    flex: '1 1 auto',
    alignItems: 'center',
    flexDirection: 'column',
    justifyContent: 'center',
    [theme.breakpoints.up(layoutQuery)]: {
      px: 0,
      py: 'calc(var(--layout-header-desktop-height) + 24px)'
    },
    ...sx
  }} {...other} data-sentry-element="Box" data-sentry-component="Main" data-sentry-source-file="main.tsx">
      {renderContent}
    </Box>;
}